//登录

<template>
  <div class="login" id="login">
    <a href="javascript:;" class="log-close"><i class="icons close"></i></a>
    <div class="log-bg">
      <div class="log-cloud cloud1"></div>
      <div class="log-cloud cloud2"></div>
      <div class="log-cloud cloud3"></div>
      <div class="log-cloud cloud4"></div>

      <div class="log-logo">欢迎!</div>
    </div>
    <div class="log-email">
      <input
        type="text"
        placeholder="邮箱"
        :class="'log-input' + (account == '' ? ' log-input-empty' : '')"
        v-model="account"
      />
      <input
        type="password"
        placeholder="密码"
        :class="'log-input' + (password == '' ? ' log-input-empty' : '')"
        v-model="password"
      />
      <a href="javascript:;" class="log-btn" @click="login">登录</a>
    </div>
    <Loading v-if="isLoging" marginTop="-30%"></Loading>
  </div>
</template>

<script>
// @ is an alias to /src
import Loading from "@/components/Loading.vue";

export default {
  name: "Login",
  data() {
    return {
      isLoging: false,
      account: "",
      password: "",
      userInfo: {
        //保存用户信息
        nick: null,
        ulevel: null,
        uid: null,
      },
    };
  },
  components: {
    Loading,
  },
  methods: {
    //登录逻辑
    login() {
      if (this.account != "" && this.password != "") {
        this.toLogin();
      }
    },

    //登录请求
    toLogin() {
      //设置在登录状态
      this.isLoging = true;

      setTimeout(() => {
        this.getUserInfo();
        let expireDays = 1000 * 60 * 60 * 24 * 15;
        window.setCookie("session", "response.data.session", expireDays);
        this.$router.push("/user");
      }, 1000);
    },
    //请求用户的一些信息
    getUserInfo() {
      //发送http请求获取，这里写死作演示
      this.userInfo = {
        nick: "Doterlin",
        ulevel: 20,
        uid: "10000",
      };

      //实例开发中这里会向服务端请求数据
      //如下(用了vue-resource):
      /*ts.$http.get(url, {
        //参数
        "params":{}
      }).then((response) => {
        //Success
      }, (response) => {
        //Error
      });*/

      //提交mutation到Store
      this.$store.commit("updateUserInfo", this.userInfo);
    },
  },
};
</script>

